<template>
  <svg
    :width="strSize.width"
    :height="strSize.height"
    viewBox="0 0 24 24"
    class="inline"
  >
    <path :d="icon" />
  </svg>
</template>

<script>
import { computed } from 'vue';

export default {
  props: {
    icon: {
      type: String,
      required: true,
    },
    size: {
      type: Number,
      default: 24,
    },
    iconColor: {
      type: String,
      default: '',
    },
  },
  setup(props) {
    return {
      strSize: computed(() => ({ width: `${props.size}px`, height: `${props.size}px` })),
      fill: computed(() => (!props.iconColor ? 'var(--hightlight-color)' : props.iconColor)),
    };
  },
};
</script>

<style lang="scss" scoped>
svg {
  fill: v-bind(fill);
}
</style>
