<template>
  <ThemeSwitcher @update-theme-color="updateThemeColor" />
  <div
    class="container mx-auto theme md:min-h-screen md:flex items-center py-4 px-4"
    :class="{
      [theme]: true,
    }"
  >
    <!-- <CVMenu /> -->
    <div class="grid grid-cols-12 w-full">
      <CVSidebar
        class="
          col-span-12
          sticky -top-[1px]
          z-10
          bg-[#222]
          shadow-xl
          md:static
          md:col-span-1
          md:mt-8
          md:bg-transparent
          md:shadow-none
          "
      />
      <CVInfo class="col-span-12 md:col-span-4 shadow-2xl" />
      <ContentSliders
        class="col-span-12 md:col-span-7 overflow-hidden md:overflow-y-auto content"
      />
    </div>
  </div>
</template>

<script>
// import CVMenu from './menu/CVMenu.vue';
import { ref } from 'vue';
import CVSidebar from './sidebar/CVSidebar.vue';
import ContentSliders from './sliders/ContentSliders.vue';
import CVInfo from './info/CVInfo.vue';
import ThemeSwitcher from './themeSwitcher/ThemeSwitcher.vue';

export default {
  name: 'CVDashboard',
  components: {
    // CVMenu,
    CVSidebar,
    CVInfo,
    ContentSliders,
    ThemeSwitcher,
  },
  setup() {
    const theme = ref('');
    function updateThemeColor(color) {
      theme.value = color ? `theme--${color}` : '';
    }

    return {
      updateThemeColor,
      theme,
    };
  },
};
</script>

