<script>
import { computed, defineAsyncComponent } from 'vue';

import useSliders from '@/compositions/sliders';

import CVAbout from '@/components/sliders/content/CVAbout.vue';

const CVSkills = defineAsyncComponent(() => import(/* webpackChunkName: "slider-skills" */ '@/components/sliders/skills/CVSkills.vue'));
const CVExperiences = defineAsyncComponent(() => import(/* webpackChunkName: "slider-experiences" */ '@/components/sliders/experiences/CVExperiences.vue'));
const CVEducation = defineAsyncComponent(() => import(/* webpackChunkName: "slider-education" */ '@/components/sliders/education/CVEducation.vue'));

export default {
  name: 'ContentSliders',
  components: {
    CVAbout,
    CVSkills,
    CVExperiences,
    CVEducation,
  },
  setup() {
    const { currentSlideName } = useSliders();
    return {
      componentName: computed(() => currentSlideName.value),
    };
  },
};
</script>

