<template>
  <div class="lottie">
    <lottie-player
      src="https://assets5.lottiefiles.com/packages/lf20_oh2gspuc.json"
      background="transparent"
      speed="1"
      style="width: 100%; height: 250px;"
      loop
      autoplay
    />
  </div>
</template>

<script>
export default {
  name: 'LottieAnimation',
  mounted() {
    this.loadScript();
  },
  methods: {
    loadScript() {
      if (!document.getElementById('lottie-player')) {
        const lottieScript = document.createElement('script');
        lottieScript.setAttribute('id', 'lottie-player');
        lottieScript.setAttribute('src', 'https://unpkg.com/@lottiefiles/lottie-player@latest/dist/lottie-player.js');
        document.head.appendChild(lottieScript);
      }
    },
  },
};
</script>
