<template>
  <div class="typewriter" ref="typewriter">
    {{ curText }}
  </div>
</template>

<script>
export default {
  name: 'AnimationText',
  data: () => ({
    curText: '',
    curIdx: 0,
  }),
  props: {
    texts: {
      type: Array,
      default: () => [''],
    },
    typeSpeed: {
      type: String,
      default: '4.5s',
    },
    carretBlinkSpeed: {
      type: String,
      default: '.75s',
    },
  },
  computed: {
    numberOfTexts() {
      return this.texts.length;
    },
  },
  mounted() {
    this.curText = this.texts[this.curIdx];
    const { typewriter } = this.$refs;

    typewriter.addEventListener('animationiteration', (e) => {
      if (e.animationName.includes('typing')) {
        if (this.curIdx < this.numberOfTexts - 1) {
          this.curIdx += 1;
        } else {
          this.curIdx = 0;
        }
        this.curText = this.texts[this.curIdx];
      }
    });
  },
};
</script>

<style lang="scss" scoped>
// https://css-tricks.com/snippets/css/typewriter-effect/
.typewriter {
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: .15em solid orange; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: .15em; /* Adjust as needed */
  animation:
    typing v-bind(typeSpeed) steps(40, end) infinite,
    blink-caret v-bind(carretBlinkSpeed) step-end infinite;
}

/* The typing effect */
@keyframes typing {
  0% {
    width: 0;
  }
  50% {
    width: 100%;
  }
  100% {
    transform: translate(0, 0);
  }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: orange; }
}
</style>
