<script>
import { ref, onMounted } from 'vue';

import IconAndText from '@/components/icons/IconAndText.vue';

export default {
  components: {
    IconAndText,
  },
  props: {
    icon: {
      type: String,
      required: true,
    },
    size: {
      type: Number,
      default: 38,
    },
    text: {
      type: String,
      required: true,
    },
  },
  setup() {
    const show = ref(false);

    onMounted(() => {
      show.value = true;
    });

    return {
      show,
    };
  },
};
</script>

